import { AddAreaService } from './../../services/AddAreaService';
import { Component, OnInit, ViewChild } from '@angular/core';
import { DatatableComponent } from '@swimlane/ngx-datatable';
import { MeasurementAndDosageSizesService } from './../../services/MeasurementAndDosageSizesService';
import swal from 'sweetalert2';

import { Location } from '@angular/common';
import { GetChemistDataService } from '../../services/GetChemistDataService';
import { Validations } from '../../../validations';
import { LogisticsService } from '../../services/logisticsService';
import { PendingLogistics } from '../../models/pendingLogistics';
declare var jQuery: any;
import { ChangeDetectorRef } from '@angular/core'
import { UserService } from '../../services/userService';




@Component({
  selector: 'logistic',
  templateUrl: './add-logistic.component.html',
  styleUrls: [
    './add-logistic.component.scss',
    '../../../../assets/icon/icofont/css/icofont.scss'
  ],
  providers: [UserService, AddAreaService, GetChemistDataService, MeasurementAndDosageSizesService, LogisticsService]
})
export class LogisticComponent implements OnInit {

  public CompanyName: string = "";
  public ContactPersonName: string = "";
  public addLoader: boolean = false;

  public pendingLogistics: PendingLogistics[] = [];
  public SingleLogistic: PendingLogistics;
  public Address: string = "";
  public Email: string = "";
  public MobileNumber: number = 0;
  public City: number = 0;
  public Area: number = 0;

  public companyname_id_error: boolean = false;
  public contact_person_id_error: boolean = false;
  public mobilenumber_id_error: boolean = false;
  public address_id_error: boolean = false;
  public fetchDataLoader: boolean = false;
  public image_error: boolean = false;

  public cities_array = [];
  public areas_array = [];
  public chemists_array = [];
  rowsFilter = [];
  tempFilter = [];

  public ProfilePicture: File;
  public LogoUrl: any;
  public runner: boolean = false;
  public singleLogisticForm: boolean = false;

  public validations: Validations;


  @ViewChild(DatatableComponent) logisticList: DatatableComponent;


  constructor(public _AddAreaNameService: AddAreaService,
    public _logisticsService: LogisticsService,
    private ref: ChangeDetectorRef,
    public _userService: UserService,
    public _getChemistDataService: GetChemistDataService, public _measurementAndDosageSizesService: MeasurementAndDosageSizesService) {

    this.validations = new Validations();

  }


  ngOnInit() {
    this.SingleLogistic = new PendingLogistics();
    this._logisticsService.getListOfPendingLogistics().subscribe(a => {
      this.pendingLogistics = a.data;
    })


  }


  getLogisticData(elem) {
    this.fetchDataLoader = true;
    this.singleLogisticForm = false;
    this._logisticsService.getDetailsForThisLogistic(elem).subscribe(a => {
      console.log(a);
      if (a.code == 200) {
        this.SingleLogistic = a.data;
        this.singleLogisticForm = true;
        this.fetchDataLoader = false;
        this.getAllCities();
        this.getCityAreas();
        
      }
    })




  }





  changeImage() {
    $("#imgInp").click();
  }

  changeimage(elem) {
    this.readURL(elem);
  }
  readURL(input) {


    var reader: any,
      target: EventTarget;
    reader = new FileReader();

    input = document.getElementById('imgInp');
    if (input.files && input.files[0]) {
      // var reader = new FileReader();
      var self = this;
      reader.onload = function (e) {
        console.log(e);
        $('#blah').attr('src', e.target.result);
        self.ProfilePicture = input.files[0];
        $("#blah").css("width", "80px");
        self.saveImageService();
      }

      reader.readAsDataURL(input.files[0]);


    }
  }


  saveImageService() {

    this._userService.saveImageForPharmacy(this.ProfilePicture).subscribe(a => {
      console.log(a);
      this.LogoUrl = a.data;
      this.SingleLogistic.ProfileURL = a.data;

    })

  }

  onSelect({ selected }) {
    console.log(selected);
  }

  onActivate(event) { }

  updateFilter(event) {
    const val = event.target.value.toLowerCase();
    console.log(val);
    // filter our data
    const temp = this.tempFilter.filter(function (d) {
      return d.LogisticsName.toLowerCase().indexOf(val) !== -1 || !val;
    });

    // update the rows
    this.rowsFilter = temp;
    // Whenever the filter changes, always go back to the first page
    this.logisticList.offset = 0;
  }

  getAllCities() {
    this._getChemistDataService.getAllCitiesService().subscribe(
      response => {
        this.cities_array = response.data;

      });
  }// End of Get All Cities

  getCityAreas() {
    this._AddAreaNameService.getAreaName(this.SingleLogistic.Logistics.CityId).subscribe(
      response => {
        this.areas_array = response.data;
      });
  }// End of Get Areas of City


  getCityAreasByThisCity(elem){
    this.SingleLogistic.Logistics.CityId = elem;
    this.getCityAreas();
 }

 getAreaSelected(ele){
  this.SingleLogistic.Logistics.AreaId = ele;
 }



  resetValues() {

    this.companyname_id_error = false;
    this.contact_person_id_error = false;
    this.mobilenumber_id_error = false;
    this.address_id_error = false;
    this.image_error = false;
  }


  addLogistic() {


    var error = false;
    this.resetValues();

    if (this.SingleLogistic.ProfileURL  == undefined) {
      this.image_error = true;
      error = true;
    }

    if (!this.validations.verifyNameInputs(this.SingleLogistic.Logistics.LogisticsName)) {
      error = true;
      this.companyname_id_error = true;
    }

    if (!this.validations.verifyNameInputs(this.SingleLogistic.Logistics.ContactPersonName)) {
      error = true;
      this.contact_person_id_error = true;
    }

    if (!this.validations.verifyNameInputs(this.SingleLogistic.MobileNumber)) {
      error = true;
      this.mobilenumber_id_error = true;
    }

    if (!this.validations.verifyNameInputs(this.SingleLogistic.Logistics.Address)) {
      error = true;
      this.address_id_error = true;
    }


    if (error) {
    
    } else {

      this.addLoader = true;
      this._logisticsService.addLogisticAutomatically(this.SingleLogistic).subscribe(Response => {
        console.log(Response);
        if (Response.code = 200) {
          swal(Response.message, "Logistic Created Successfully", "success");
          this.addLoader = false;
          this.singleLogisticForm = false;
          $('#logisticID').prop('selectedIndex',0);
        }else{
          swal(Response.message, "Logistic not created", "error");
          this.addLoader = false;
        }
      }, err => {
        console.log(err);
        var obj = JSON.parse(err._body);
        swal(obj.message, "Logistic not created", "error");
      });



    }


  }

}



