
    export interface City {
        Id: number;
        Name: string;
    }

    export interface Area {
        City: City;
        Id: number;
        Name: string;
        CityId: number;
        Latitude: number;
        Longitude: number;
    }

    export interface City2 {
        Id: number;
        Name: string;
    }

    export interface Logistics {
        Area: Area;
        City: City2;
        Id: number;
        ContactPersonName: string;
        LogisticsName: string;
        Address: string;
        Latitude?: any;
        Longitude?: any;
        CityId?: number;
        AreaId?: number;
        IsActive: boolean;
    }

    export class PendingLogistics {
        addresses: any[];
        Chemist?: any;
        Logistics: Logistics;
        shoppingCarts: any[];
        Id: number;
        UserName?: any;
        FullName?: any;
        Email: string;
        Password?: any;
        IsBlocked: boolean;
        UserType: number;
        MobileNumber: string;
        FCMId?: any;
        isNumberVerified: boolean;
        ProfileURL: string;
        LastUsedIP?: any;
        GUID: string;
        UserRole: number;
        LogisticsId: number;
        ChemistId?: any;
    }




